import React, { useEffect, useRef, useState } from 'react';
import gsap from 'gsap';

import { useTrackedState } from '../../Store';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import calcularPropiedadesScrollTrigger from '../../js/utilities/calcularPropiedadesScrollTrigger';

import CambioImagenScroll from '../../components/CambioImagenScroll';
import DownloadButton from '../../components/Btn-descarga';

const PerdidaEcosistemasActividadHumana = () => {
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        return (
            <>
                {language === 'es' &&
                    <>
                        <h2>Proceso histrico de prdida de ecosistemas</h2>
                        <p>Para evaluar el impacto humano en los ecosistemas, es necesario conocer  el proceso, las tasas histricas y los patrones espaciales mediante los cuales los usos de la tierra desplazan y reemplazan los ecosistemas originales. Generar buena informacin sobre las tendencias histricas y proyecciones futuras de la transformacin y degradacin de los ecosistemas es esencial para evaluar el estado y nivel de riesgo acumulado de los ecosistemas de modo que ayude a orientar su gestin.</p>
                        <p>El proceso de evaluacin de riesgo de la LRE requiere de informacin histrica del estado de los ecosistemas, requiere de un referente emprico temporal de los ltimos 50 aos y de proyecciones futuras a 50 aos desde un histrico de referencia. Para este fin, con base en informacin satelital y aerofotogrfica disponible, se construy una serie multitemporal de mapas (1970, 1990, 2000 y 2014) que indican cmo ha avanzado la conversin de los ecosistemas naturales durante los ltimos 45 aos. Estos mapas muestran, adems de procesos de transformacin de alto contraste, como la deforestacin, tambin procesos ms conspicuos, como el reemplazo y degradacin de ecosistemas de sabanas y pramos.</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <h2>Historic process of ecosystem loss</h2>
                        <p>To assess the human impact on ecosystems, it is necessary to know the types of land use processes involved, and the historical rates and spatial patterns by which land uses displace and replace the original ecosystems. To generate good quality information on historical trends and future projections of the transformation and degradation of ecosystems, is essential to assess the status and level of accumulated risk of ecosystems, that can help guide their management.</p>
                        <p>The RLE risk assessment process requires historic information regarding the ecosystem condition; it also requires an empirical time benchmark from the past 50 years as well as projections for the future 50 years based on the historic benchmark. To this end, a multi-temporal series of maps (1970, 1990, 2000, 2014) were constructed based on available satellite and aero-photographic information. These maps indicate how the conversion of natural ecosystems has taken place over the past 45 years. They not only show high contrast transformation processes, such as deforestation, but also more conspicuous processes, such as the replacement and degradation of savannas and moorlands ecosystems.</p>
                    </>
                }
            </>
        )
    }


    const state = useTrackedState();
    const { language } = state;
    const [heightContainer, containerRef, headerSpace] = useHeightContainer();

    const refContenido = useRef();
    const refImagenes = useRef();

    const [positionBoton, setPositionBoton] = useState();

    const numImagenesScroll = 5;

    useEffect(() => {
        if (refContenido.current !== undefined) {
            setTimeout(() => {
                var scroll = ScrollTrigger.create({
                    end: "bottom bottom",
                    // markers: true,
                    pin: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-historico", refImagenes, ".imagenes-scroll-historico", numImagenesScroll, headerSpace).pin,
                    pinSpacing: false,
                    start: () => calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-historico", refImagenes, ".imagenes-scroll-historico", numImagenesScroll, headerSpace).start,
                    trigger: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-historico", refImagenes, ".imagenes-scroll-historico", numImagenesScroll, headerSpace).trigger,
                    invalidateOnRefresh: true,
                    onEnter: () => {
                        setPositionBoton("fixed");
                    },
                    onEnterBack:()=>{
                        setPositionBoton("fixed");
                    },
                    onLeave: () => {
                        setPositionBoton("relative");
                    },
                    onLeaveBack: () => {
                        setPositionBoton("relative");
                    },
                })
            }, 200);

        }

    }, [])


    return (
        <div id="perdida-de-ecosistemas" className="proceso-historico-perdida-de-ecosistemas">
            <div className="container-fluid row mx-0 justify-content-end align-items-start pt-5 px-0">
                <div className="col-lg-6 pl-5 d-flex justify-content-lg-end">
                    <div
                        className="introduccion-textos introduccion-textos-historico"
                        ref={refContenido}
                    >
                        <Textos />
                    </div>
                </div>

                <div
                    className="col-lg-8 px-0 imagenes-scroll imagenes-scroll-historico imagenes-perdida-ecosistemas m-0"
                    ref={refImagenes}
                >
                    <CambioImagenScroll
                        carpeta="perdida_ecosistema"
                        claseMapa="img-mapa-pe"
                        idioma={language}
                        nombreImagen="perdida_ecosistema_actividad_humana"
                        numMapas={numImagenesScroll}
                    />
                    <div className="d-none">
                        <CambioImagenScroll
                            carpeta="perdida_ecosistema"
                            claseMapa="img-mapa-pe"
                            idioma={language}
                            nombreImagen="perdida_ecosistema_actividad_humana"
                            numMapas={numImagenesScroll}
                        />
                    </div>
                </div>

                <DownloadButton
                    linkDescarga={"https://drive.google.com/drive/folders/1GDbP_KMPNVQZBB7rtddj7We_Ntv5-QNJ?usp=sharing"}
                    position={positionBoton}
                    tieneMargin={true}
                />

            </div>

        </div>
    )

}

export default PerdidaEcosistemasActividadHumana;
