import React, { useEffect, useRef } from 'react';
import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import useIsMobile from '../../hooks/useIsMobile';

const Autores = () => {
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        return (
            <>
                {language === 'es' &&
                    <>
                        <div className="pl-lg-0 pr-lg-2 mt-lg-3">
                            <div>
                                <p>
                                    <strong>Andrs Etter</strong> <br />
                                    Profesor titular de la Pontificia Universidad Javeriana. Bilogo de la Universidad de los Andes, MSc en Ecologa del Paisaje del ITC (Holanda), PhD en Ecologa de la Universidad de Queensland. Es miembro de la Comisin de Manejo Ecosistmico (UICN) y director del estudio de Lista Roja de Ecosistemas de Colombia.
                                    </p>
                            </div>
                            <div>
                                <p>
                                    <strong>ngela Andrade</strong> <br />
                                    Presidente de la Comisin de Gestin de Ecosistemas (UICN). Antroploga de la Universidad de los Andes, MSc en Ecologa del Paisaje del ITC (Holanda). Es miembro de Conservacin Internacional-Colombia, experta en Adaptacin al Cambio Climtico y Poltica Ambiental Global.
                                    </p>
                            </div>
                            <div>
                                <p>
                                    <strong>Kelly Saavedra</strong> <br />
                                    Ecloga de la Pontificia Universidad Javeriana. Es especialista en sistemas de informacin geogrfica y consultora ambiental.
                                    </p>
                            </div>
                            <div>
                                <p>
                                    <strong>Paula Amaya</strong> <br />
                                    Ecloga de la Pontificia Universidad Javeriana. Es especialista en sistemas de informacin geogrfica y consultora ambiental.
                                    </p>
                            </div>
                            <div>
                                <p>
                                    <strong>Juliana Corts</strong> <br />
                                    Ecloga de la Pontificia Universidad Javeriana, MSc en Conservacin de Biodiversidad de la Universidad de Barcelona. Pertenece al Grupo de Especialista de Primates en Fragmentos (UICN). Es investigadora en gestin territorial del Instituto Alexander von Humboldt.
                                    </p>
                            </div>
                            <div>
                                <p>
                                    <strong>Paulo Arvalo</strong> <br />
                                    Eclogo de la Pontificia Universidad Javeriana, doctor en Tierra y Ambiente de la Universidad de Boston. Es investigador posdoctoral en la Universidad de Boston.
                                    </p>
                            </div>
                        </div>

                        <h2 className="mb-3">Agradecimientos</h2>
                        <div className="pl-lg-0 pr-lg-2">

                            <p> Este libro no hubiera sido posible sin la colaboracin de Fabio Arjona, Brigitte Baptiste, Edmund Barrow, Hernando Garca, David Keith, Tito Muto, Emily Nicholson, Jon Paul Rodrguez e Irene Zager, a quienes agradecemos su inters y entusiasmo.</p>

                            <p>De igual forma, agradecemos a las instituciones que financiaron la investigacin: la Fundacin Moore, la Fundacin MAVA, la Unin Internacional para la Conservacin de la Naturaleza (UICN), la Pontificia Universidad Javeriana, Conservacin Internacional-Colombia y el Instituto Alexander von Humboldt.</p>
                        </div>

                    </>
                }
                {
                    language === 'en' &&
                    <>

                        <div className="pl-lg-0 pr-lg-2">
                            <div>
                                <p>
                                    <strong>Andrs Etter</strong> <br />
                                    Professor, Faculty of Environmental and Rural Studies, Pontificia Universidad Javeriana. Biologist, Universidad de los Andes. MSc in Landscape Ecology (ITC, Netherlands). PhD in Ecology (University of Queensland, Australia). PI of the Colombian study for the Red List of Ecosystems, Member IUCN Ecosystem Management Commission.
                                    </p>
                            </div>
                            <div>
                                <p>
                                    <strong>ngela Andrade</strong> <br />
                                    President, IUCN Commission on Ecosystem Management. Anthropologist, Universidad de los Andes. MSc in Landscape Ecology (ITC, Netherlands). Expert in Adaptation to Climate Change and Global Environmental Policy, International Conservation-Colombia.
                                    </p>
                            </div>
                            <div>
                                <p>
                                    <strong>Kelly Saavedra</strong> <br />
                                    Ecologist, Pontificia Universidad Javeriana, Geographic Information Systems specialist. Environmental Consultant.
                                    </p>
                            </div>
                            <div>
                                <p>
                                    <strong>Paula Amaya</strong> <br />
                                    Ecologist, Pontificia Universidad Javeriana. Geographic Information Systems specialist. Environmental Consultant.
                                    </p>
                            </div>
                            <div>
                                <p>
                                    <strong>Juliana Corts</strong> <br />
                                    Ecologist, Pontificia Universidad Javeriana. MSc in Biodiversity Conservation (University of Barcelona). Specialist Group on Primates in Fragments (IUCN). Researcher, Instituto Alexander von Humboldt.
                                    </p>
                            </div>
                            <div>
                                <p>
                                    <strong>Paulo Arvalo</strong> <br />
                                    Ecologist, Pontificia Universidad Javeriana. PhD in Land and Environment (University of Boston). Postdoctoral researcher at University of Boston.
                                    </p>
                            </div>
                        </div>
                        <h2 className="mb-3">Acknowledgements</h2>
                        <div className="pl-lg-0 pr-lg-2">

                            <p>This book would not have been possible without the help and collaboration of Fabio Arjona, Brigitte Baptiste, Edmund Barrow, Hernando Garca, David Keith, Tito Muto, Emily Nicholson, Jon Paul Rodrguez, and Irene Zager, to whom we thank for their interest and enthusiasm.</p>

                            <p>Likewise, we thank the institutions that financed the research: The Gordon and Betty Moore Foundation, the MAVA Foundation, the International Union for Conservation of Nature (IUCN), the Pontificia Universidad Javeriana, Conservacin Internacional-Colombia, and the Institute Alexander von Humboldt.</p>
                        </div>
                    </>
                }
            </>
        )

    }
    const state = useTrackedState();
    const { language } = state;
    const isMobile = useIsMobile();
    const [heightContainer, containerRef, headerSpace] = useHeightContainer();


    return (
        <div id="autores" className="creditos">
            <div className="container-fluid row flex-lg-nowrap mx-0 justify-content-end py-5 align-items-start px-0">

                <div className="col-lg-6 pl-5 d-flex justify-content-center px-5">
                    <div className="introduccion-textos texto-autores">
                        <div className="col-3 px-0 mb-3">
                            <img className="col-12 px-0" src="./img/logos/red_list.png" alt="signo de admiracin" />
                        </div>
                        <div className="title-sticky"
                            style={{
                                top: `${headerSpace}px`
                            }}
                        >
                            {language === 'es' &&
                                <>
                                    <h2 className="mb-3">Autores</h2>
                                </>
                            }
                            {language === 'en' &&
                                <>
                                    <h2 className="mb-3">Authors</h2>
                                </>
                            }

                        </div>
                        <Textos />
                    </div>
                </div>

                <div
                    className="col-lg-8 px-0 imagenes-scroll imagenes-scrolll-autores m-0 mt-5 mt-lg-0"
                    style={{
                        position: "sticky",
                        top: heightContainer / 2
                    }}
                >
                    <div
                        ref={containerRef}
                        style={{
                            height: isMobile ? "auto" : `${heightContainer}px`
                        }}
                    >
                        <img src={`./img/introduccion/ambiente3.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
                    </div>
                </div>
            </div>
        </div >
    )
}

export default Autores;