import React, { useEffect, useRef } from 'react';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import ReactTooltip from 'react-tooltip';

import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import calcularPropiedadesScrollTrigger from '../../js/utilities/calcularPropiedadesScrollTrigger';

import CambioImagenScroll from '../../components/CambioImagenScroll';

const CriteriosEvaluacion = () => {
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        return (
            <>
                <ReactTooltip
                    id="tooltip-criterios-evaluacion"
                    place="top"
                    html={true}
                    clickable={true}
                    effect={'solid'}
                    type={'info'}
                    event={'click'}
                    globalEventOff={'click'}
                    className={'nota-al-pie'}
                />
                {language === 'es' &&
                    <>
                        <h2>Criterios de evaluacin y categoras de riesgo</h2>
                        <p>El modelo de evaluacin de riesgos agrupa los sntomas de colapso del ecosistema en cuatro tipos principales e identifica los mecanismos correspondientes que vinculan los sntomas con el riesgo de que un ecosistema pierda sus caractersticas definitorias (biota nativa caracterstica o procesos ecolgicos) <span className="nota-al-pies-number" data-tip="<span>[6]</span>. Keith, D. A. et al. (2013). Scientific Foundations for an IUCN Red List of Ecosystems. <em>PLoS ONE</em>, 8(5). doi: 10.1371/journal.pone.0062111" data-for="tooltip-criterios-evaluacion">[6]</span>.</p>
                        <p>Cuando ocurren interacciones entre dos o ms de estos mecanismos, pueden producir sntomas adicionales de transicin hacia una situacin de colapso del ecosistema, lo que lleva a la prdida de su identidad, biodiversidad y funcin, y como consecuencia a la prdida de los servicios que provee. Mltiples mecanismos e interacciones pueden integrarse en un modelo de simulacin de dinmica del ecosistema para producir estimaciones integradas cuantitativas del riesgo de colapso (criterio E, el cual no se incluy an en este estudio). </p>
                        <p>Los cambios en la extensin y severidad de los criterios A, C y D se miden para diferentes marcos de tiempo dependiendo de la disponibilidad de datos: i) histrico (desde 1750); ii) presente (durante los ltimos 50 aos); iii) futuro (durante cualquier perodo de 50 aos, incluidos el presente y el futuro), y iv) futuro (durante los prximos 50 aos). El estado general de riesgo es asignado en funcin del nivel ms alto alcanzado por cualquiera de los criterios de evaluacin, ya que el riesgo global se considera que est determinado por el factor ms limitante.</p>
                    </>
                }
                {language === 'en' &&
                    <>
                        <h2>Evaluation Criteria and Risk Categories</h2>
                        <p>The risk assessment model groups the symptoms of ecosystem collapse into four main types, and identifies the corresponding mechanisms that link the symptoms with the risk that an ecosystem will lose its defining characteristics (characteristic native biota or ecological processes) <span className="nota-al-pies-number" data-tip="<span>[6]</span>. Keith, D. A. <em>et al.</em> (2013). Scientific Foundations for an IUCN Red List of Ecosystems. <em>PLoS ONE</em>, 8(5). doi: 10.1371/journal.pone.0062111" data-for="tooltip-criterios-evaluacion">[6]</span>.</p>
                        <p>When interactions occur between two or more of these mechanisms, they can produce additional symptoms leading to a situation of ecosystem collapse, producing larger loss of their identity, biodiversity and function, and as a consequence the loss of the services they provide. Multiple mechanisms and interactions can be integrated into a simulation model of ecosystem dynamics to produce quantitative integrated estimates of the risk of collapse (criterion E, which was not yet included in this study).</p>
                        <p>The changes in extent and severity of the criteria A, C and D are measured for different timeframes depending on data availability: i) Historic (since 1750); ii) Present (over the past 50 years); iii) Future (over any 50-year period including the present and future), and iv) Future (over the next 50 years). The general risk status is assigned based on the highest level reached by any of the evaluation criteria, since the overall risk is considered to be determined by the most limiting factor.</p>
                    </>
                }
            </>
        )
    }


    const state = useTrackedState();
    const { language } = state;
    const [heightContainer, containerRef, headerSpace] = useHeightContainer();
    const refContenido = useRef();
    const refImagenes = useRef();
    const numImagenesScroll = 4;


    useEffect(() => {
        if (refContenido.current !== undefined) {
            setTimeout(() => {
                var scroll = ScrollTrigger.create({
                    end: "bottom bottom",
                    // markers: true,
                    pin: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-criterios-evaluacion", refImagenes, ".imagenes-scroll-criterios-evaluacion", numImagenesScroll + 1, headerSpace).pin,
                    pinSpacing: false,
                    start: () => calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-criterios-evaluacion", refImagenes, ".imagenes-scroll-criterios-evaluacion", numImagenesScroll + 1, headerSpace).start,
                    trigger: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-textos-criterios-evaluacion", refImagenes, ".imagenes-scroll-criterios-evaluacion", numImagenesScroll + 1, headerSpace).trigger,
                    invalidateOnRefresh: true
                })
            }, 100);

        }
    }, [refContenido.current])


    return (
        <div id="criterios-de-evaluacion-y-categorias-de-riesgo">
            <div className="container-fluid row mx-0 justify-content-end align-items-start pt-5 px-0">
                <div className="col-lg-6 pl-5 d-flex justify-content-lg-end">
                    <div
                        className="introduccion-textos introduccion-textos-criterios-evaluacion"
                        ref={refContenido}
                    >
                        <Textos />
                    </div>
                </div>


                <div
                    className="col-lg-8 position-relative px-0 imagenes-scroll imagenes-scroll-criterios-evaluacion m-0"
                    ref={refImagenes}
                >
                    <div>
                        <img src={`./img/perdida_ecosistema/criterios_evaluacion_${language}.jpg`} alt="imagen de ambiente 1" className="img-fluid" />
                    </div>

                    <CambioImagenScroll
                        carpeta="perdida_ecosistema"
                        claseMapa="mapa-criterios-ce"
                        headerSpace={headerSpace}
                        idioma={language}
                        nombreImagen="criterios_mapa"
                        numMapas={numImagenesScroll}
                    />

                    <div className="d-none">
                        <CambioImagenScroll
                            carpeta="perdida_ecosistema"
                            claseMapa="mapa-criterios-ce_2"
                            headerSpace={headerSpace}
                            idioma={language}
                            nombreImagen="criterios_mapa"
                            numMapas={numImagenesScroll}
                        />
                    </div>
                </div>
            </div>
        </div>
    )

}

export default CriteriosEvaluacion;
