import React, { useEffect, useRef, useState } from 'react';
import ReactTooltip from 'react-tooltip';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import calcularPropiedadesScrollTrigger from '../../js/utilities/calcularPropiedadesScrollTrigger';

import CambioImagenScroll from '../../components/CambioImagenScroll';
import DownloadButton from '../../components/Btn-descarga';

const RestauracionEcosistemas = () => {
    const Textos = () => {
        const state = useTrackedState();
        const { language } = state;
        useEffect(() => {
            ReactTooltip.rebuild();
        }, [language]);
        return (
            <>
                {language === 'es' &&
                    <>
                        <h2>Restauracin de ecosistemas</h2>
                        <div className="mt-lg-3">
                            <p>En el mundo actual, caracterizado por altos niveles de transformacin y degradacin de los ecosistemas, la restauracin ecolgica es cada vez un complemento ms indispensable para garantizar la conservacin de la biodiversidad y los servicios ecosistmicos <span className="nota-al-pies-number" data-tip="<span>[17]</span>. Wiens, J. A. y Hobbs, R. J. (2015). Integrating conservation and restoration in a changing world. <em>BioScience</em>, 65(3), 302312. Recuperado de <a href='https://doi.org/10.1093/biosci/biu235'> https://doi.org/10.1093/biosci/biu235 </a>" data-for="tooltip-restauracion-ecosistemas">[17]</span>. La restauracin ecolgica es un conjunto de procesos que buscan restituir las caractersticas de composicin y funcionamiento de los ecosistemas degradados, con el fin de recuperar los servicios que proveen.</p>
                            <p>Los altos niveles y la extensin actual de la degradacin ecolgica, sumada a los limitados recursos disponibles para recuperarla, requieren que se lleve a cabo procedimientos de priorizacin que permitan identificar aquellas reas ptimas que permitan maximizar el cumplimiento de los objetivos de la manera ms efectiva <span className="nota-al-pies-number" data-tip="<span>[18]</span>. Etter, A. et al. (2020). Assessing restoration priorities for high-risk ecosystems: An application of the IUCN red list of ecosystems. <em>Land Use Policy 99</em>, 104874. Recuperado de:<a href='https://doi.org/10.1016/j.landusepol.2020.104874'> https://doi.org/10.1016/j.landusepol.2020.104874 </a>" data-for="tooltip-restauracion-ecosistemas">[18]</span>.</p>
                            <p>Entre los criterios importantes para enfocar la seleccin de reas de inters de restauracin esta el de aquellas que presentan los mayores niveles de riesgo. La LRE no solo permite identificar los ecosistemas con mayor riesgo, sino que nos da la informacin de la ubicacin de las reas en que estos han desaparecido, y los usos de la tierra que los ha reemplazado. </p>
                            <p>Es importante tener en cuenta que los usos de la tierra deberan cumplir en principio con dos condiciones simultneamente: generar beneficios para la sociedad y minimizar los impactos sobre la base ecolgica que los sustenta para garantizar su sostenibilidad. De manera que la presencia de reas con usos poco productivos que coincidan con reas donde desaparecieron ecosistemas catalogados como de alto riesgo (CR, EN) deberan necesariamente ser foco de priorizacin para ser recuperados mediante procesos de restauracin.</p>
                        </div>
                    </>
                }
                {language === 'en' &&
                    <>
                        <h2>Ecosystem restoration</h2>
                        <div className="mt-lg-3">
                            <p>In today's world characterized by high levels of transformation and degradation of ecosystems, ecological restoration is increasingly an indispensable complement to warrant the conservation of biodiversity and ecosystem services <span className="nota-al-pies-number" data-tip="<span>[17]</span>. Wiens, J. A. and Hobbs, R. J. (2015). Integrating conservation and restoration in a changing world. <em>BioScience</em>, 65(3), 302312. Available from: <a href='https://doi.org/10.1093/biosci/biu235'> https://doi.org/10.1093/biosci/biu235 </a>" data-for="tooltip-restauracion-ecosistemas">[17]</span>. Ecological restoration is a set of processes that seek to restore the characteristics of composition and functioning of degraded ecosystems, and the services they provide.</p>
                            <p>The high levels and current extent of ecological degradation, in addition to the limited resources available to for restoration, requires that priority procedures be carried out to identify those areas that maximize the fulfilment of the objectives in the most effective way <span className="nota-al-pies-number" data-tip="<span>[18]</span>. Etter, A., Andrade, A., Nelson, C. R., Corts, J., and Saavedra, K. (2020). Assessing restoration priorities for high-risk ecosystems: An application of the IUCN red list of ecosystems. <em>Land Use Policy 99</em>, 104874. Available from:<a href='https://doi.org/10.1016/j.landusepol.2020.104874'> https://doi.org/10.1016/j.landusepol.2020.104874 </a>" data-for="tooltip-restauracion-ecosistemas">[18]</span>.</p>
                            <p>Among the important criteria to focus on the selection of areas of interest for restoration are those of ecosystems with the highest levels of risk. The RLE not only allows us to identify the current location of ecosystems at greatest risk, but may also give information about the locations where they have disappeared, and the land uses that have replaced them.</p>
                            <p>It is important to keep in mind that land uses should in principle meet two conditions simultaneously: Generate benefits for society, and minimize the impacts on the ecological base that sustains them to ensure their sustainability. Therefore, finding areas with low productivity land uses, which coincide with areas where ecosystems classified as high risk (CR, EN) disappeared, should necessarily be a focus of prioritization to be recovered through restoration processes.</p>
                        </div>
                    </>}


                <ReactTooltip
                    id="tooltip-restauracion-ecosistemas"
                    place="top"
                    html={true}
                    clickable={true}
                    effect={'solid'}
                    type={'info'}
                    event={'click'}
                    globalEventOff={'click'}
                    className={'nota-al-pie'}
                />

            </>
        )
    }

    const state = useTrackedState();
    const { language } = state;
    const [heightContainer, containerRef, headerSpace] = useHeightContainer();

    const refContenido = useRef();
    const refImagenes = useRef();

    const [positionBoton, setPositionBoton] = useState();

    const numImagenesScroll = 2;

    useEffect(() => {
        ReactTooltip.rebuild();
    }, [language]);

    useEffect(() => {
        setTimeout(() => {
            ScrollTrigger.create({
                // markers: true,
                pin: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-restauracion-ecosistemas", refImagenes, ".imagenes-scrolll-restauracion-ecosistemas", numImagenesScroll, headerSpace).pin,
                pinSpacing: false,
                start: () => calcularPropiedadesScrollTrigger(refContenido, ".introduccion-restauracion-ecosistemas", refImagenes, ".imagenes-scrolll-restauracion-ecosistemas", numImagenesScroll, headerSpace).start,
                trigger: calcularPropiedadesScrollTrigger(refContenido, ".introduccion-restauracion-ecosistemas", refImagenes, ".imagenes-scrolll-restauracion-ecosistemas", numImagenesScroll, headerSpace).trigger,
                invalidateOnRefresh: true,
                onEnter: () => {
                    setPositionBoton("fixed");
                },
                onEnterBack: () => {
                    setPositionBoton("fixed");
                },
                onLeave: () => {
                    setPositionBoton("relative");
                },
                onLeaveBack: () => {
                    setPositionBoton("relative");
                },
            })
        }, 200);
    }, [])

    return (
        <div id="restauracion-de-ecosistemas">
            <div className="container-fluid row mx-0 justify-content-end align-items-start pt-5 px-0">
                <div className="col-lg-6 pl-5 d-flex justify-content-lg-end">
                    <div
                        className="introduccion-textos introduccion-restauracion-ecosistemas"
                        ref={refContenido}
                    >
                        <Textos />
                    </div>
                </div>


                <div
                    className="col-lg-8 px-0 imagenes-scroll imagenes-scrolll-restauracion-ecosistemas m-0"
                    ref={refImagenes}
                >
                    <CambioImagenScroll
                        claseMapa="img-mapa-restauracion-ecosistemas"
                        numMapas={2}
                        nombreImagen="restauracion_ecosistemas"
                        carpeta="conservacion_areas"
                        idioma={language}
                    />
                    <div className="d-none">
                        <CambioImagenScroll
                            claseMapa="img-mapa-restauracion-ecosistemas-2"
                            numMapas={2}
                            nombreImagen="restauracion_ecosistemas"
                            carpeta="conservacion_areas"
                            idioma={language}
                        />
                    </div>
                </div>
            </div>

            <DownloadButton
                linkDescarga="https://drive.google.com/drive/folders/16M3AFWZCUJ07opcV4CthJoLEDuKUCaad?usp=sharin"
                position={positionBoton}
                tieneMargin={true}
            />
        </div>


    )
}

export default RestauracionEcosistemas;