import React, { useEffect } from 'react';
import ReactTooltip from 'react-tooltip';

import { useTrackedState } from '../../Store';
import { useHeightContainer } from '../../hooks/useHeightContainer';
import useIsMobile from '../../hooks/useIsMobile';

const Introduccion = () => {

  const Textos = () => {
    const state = useTrackedState();
    const { language } = state;
    useEffect(() => {
      ReactTooltip.rebuild();
    }, [language]);

    return (

      <>
        {language === 'es' &&

          <>
            <p className="mt-lg-3">La biosfera, de la que depende la humanidad en su conjunto, est sufriendo alteraciones sin precedentes en todos sus niveles espaciales. Los diferentes ecosistemas que la componen estn siendo impactados y transformados de manera creciente a nivel global debido al aumento de la poblacin humana y de las demandas de recursos derivadas de sus patrones de consumo. La crisis ambiental contempornea tiene su principal origen en esta acelerada transformacin y degradacin de los ecosistemas, que son el hbitat y soporte esencial de los procesos y servicios ecolgicos <span
              className="nota-al-pies-number" data-tip="<span>[1]</span>. Intergovernmental Science-Policy Platform on Biodiversity and Ecosystem Services (Ipbes). (2019). <em>Global Assessment Report on Biodiversity and Ecosystem Services.</em> Bonn: Autor. Recuperado de <a href='https://ipbes.net/global-assessment'>https://ipbes.net/global-assessment  </a>" data-for="tooltip-introduccion" >[1]</span>.</p>

            <p>La diversidad biolgica la diversidad de especies, las interacciones entre especies y la diversidad de los ecosistemas est disminuyendo a un ritmo ms rpido que nunca antes en la historia humana <span className="nota-al-pies-number" data-tip="<span>[2]</span>. Cardinale, B. J., Duffy, J. M., Gonzalez, A. y Hooper, D. U. (2012). Biodiversity loss and its impact on humanity. <em>Nature</em>, 486(7401), 5967. doi: 10.1038/nature11148" data-for="tooltip-introduccion" >[2]</span>. En la actualidad un 60 % de la superficie terrestre muestra una huella humana significativa, y se estima que entre 1000 y 6000 millones de hectreas presentan niveles de degradacin ecolgica <span className="nota-al-pies-number" data-tip="<span>[3]</span>. Gibbs, H. K. y Salmon, J. M. (2015). Mapping the worlds degraded lands. <em>Applied Geography</em>, 57, 1221. doi: 10.1016/j.apgeog.2014.11.024" data-for="tooltip-introduccion" >[3]</span>.</p>

            <p>La transformacin humana de los ecosistemas y el cambio climtico estn poniendo en riesgo la viabilidad futura de los ecosistemas, ya que estos pierden caractersticas importantes y por lo tanto la disponibilidad y calidad de los bienes y servicios ecosistmicos que dependen de estos <span className="nota-al-pies-number" data-tip="<span>[2]</span>. Cardinale, B. J., Duffy, J. M., Gonzalez, A. y Hooper, D. U. (2012). Biodiversity loss and its impact on humanity. <em>Nature</em>, 486(7401), 5967. doi: 10.1038/nature11148" data-for="tooltip-introduccion">[2]</span>. Por esta razn es de gran importancia conocer los cambios en las caractersticas y distribucin geogrfica, y el estado de conservacin de los ecosistemas.</p>

            <p>Para anticipar y actuar efectivamente ante este escenario problemtico, necesitamos entender los niveles de colapso de los ecosistemas, puesto que estos son las unidades de referencia fundamentales para la gestin territorial, que expresan de manera sinttica cambios en la composicin, funcionamiento y estabilidad de los sistemas ecolgicos. Esto ha sido reconocido en las convenciones ambientales mundiales (p. e., en la Convencin de Diversidad Biolgica [CBD] <span className="nota-al-pies-number" data-tip="<span>[4]</span>. Convention of Biological Diversity (CBD). (2010). <em>Strategic Plan for Biodiversity 2011-2020 and the Aichi Biodiversity Targets</em>. Recuperado de <a href='https://www.cbd.int/doc/meetings/cop/cop-10/official/cop-10-27-add1-en.pdf'> https://www.cbd.int/doc/meetings/cop/cop-10/official/cop-10-27-add1-en.pdf </a>" data-for="tooltip-introduccion" >[4]</span> y en el Acuerdo de Pars <span className="nota-al-pies-number" data-tip="<span>[5]</span>. United Nations Climate Change Convention (UNFCCC). (2015).The Paris Agreement. Recuperado de <a href='https://unfccc.int/process-and-meetings/the-paris-agreement/the-paris-agreement'>https://unfccc.int/process-and-meetings/the-paris-agreement/the-paris-agreement </a>" data-for="tooltip-introduccion">[5]</span>), que han identificado a la conservacin de los ecosistemas y al mantenimiento de su integridad como un objetivo esencial para garantizar la salvaguarda de especies, procesos ecolgicos y de los servicios ecosistmicos de los que la sociedad depende.</p>

            <p>La Lista Roja de Ecosistemas (LRE), desarrollada y adoptada por la Unin Internacional de Conservacin de la Naturaleza (UICN) en 2014, es un nuevo estndar global para monitorear y documentar el estado de los ecosistemas <span className="nota-al-pies-number" data-tip="<span>[6]</span>. Keith, D. A. et al. (2013). Scientific Foundations for an IUCN Red List of Ecosystems. <em>PLoS ONE</em>, 8(5). doi: 10.1371/journal.pone.0062111" data-for="tooltip-introduccion" >[6]</span>. Ella es parte esencial de las herramientas desarrolladas para evaluar los riesgos de prdida de la biodiversidad y est orientada a apoyar la gestin del uso de la tierra y la conservacin. La LRE tambin es un complemento esencial de la Lista Roja de Especies de la UICN, que ha sido ampliamente implementada a nivel mundial, ya que representa la condicin ecolgica a gran escala de las interacciones entre especies y otros procesos ecolgicos importantes.</p>

            <p>Las categoras y los criterios de la LRE estn diseados para ser aplicables de manera amplia a todos los tipos de ecosistemas y reas geogrficas del mundo. Tambin lo estn para que la LRE sea transparente, cientficamente rigurosa, y fcil de entender por los responsables polticos y el pblico, quienes son los que finalmente toman las decisiones de su gestin. Si bien un objetivo principal del LRE se centra en la conservacin de la biodiversidad, esta puede informar una amplia gama de otras actividades, incluida la gestin sostenible de los servicios ecosistmicos.</p>

            <p>La meta del programa de la LRE de la UICN es la evaluacin del estado global de los ecosistemas terrestres, marinos, de agua dulce y subterrneos. Tambin busca apoyar el desarrollo de listas rojas nacionales y regionales, para poder garantizar que toda la informacin de reportes nacionales pueda alimentar de manera ordenada los reportes globales de indicadores ambientales y aporten a la planificacin global de la conservacin y la sostenibilidad.</p>

            <p>Para Colombia, un pas en el que la diversidad biolgica de ecosistemas y especies es excepcional y de un alto valor estratgico, la implementacin del estndar de la LRE es de mucha importancia. Varios estudios anteriores han mostrado el proceso de deterioro de los ecosistemas colombianos debido a la expansin de la frontera agrcola, los usos inadecuados de la tierra y la extraccin de recursos <span className="nota-al-pies-number" data-tip="<span>[7]</span>. Chaves, S. M. E., Arango, V. N. e Instituto de Investigacin de Recursos Biolgicos Alexander von Humboldt. (1998). <em>Informe nacional sobre el estado de la biodiversidad: Colombia, 1997 </em> (3 vols.). Bogot: Instituto de Investigacin de Recursos Biolgicos Alexander von Humboldt, Ministerio del Medio Ambiente y Programa de las Naciones Unidas para el Medio Ambiente." data-for="tooltip-introduccion">[7]</span>, <span className="nota-al-pies-number" data-tip="<span>[8]</span>. Romero, M., Cabrera, E. y Ortiz, N. (2008). <em>Informe sobre el estado de la biodiversidad en Colombia 2006-2007</em>. Bogot: Instituto de Investigacin de Recursos Biolgicos Alexander von Humboldt. Recuperado de <a href='http://www.humboldt.org.co/es/test/item/336-informe-sobre-el-estado-de-la-biodiversidad-en-colombia-2006-2007'>http://www.humboldt.org.co/es/test/item/336-informe-sobre-el-estado-de-la-biodiversidad-en-colombia-2006-2007</a>" data-for="tooltip-introduccion">[8]</span>, <span className="nota-al-pies-number" data-tip="<span>[9]</span>.Etter, A. Mcalpine, C., Seabrook, L. y Wilson, A. (2011). Incorporating temporality and biophysical vulnerability to quantify the human spatial footprint on ecosystems. <em>Biological Conservation</em>, 144(5), 15851594. Recuperado de <a href='https://doi.org/10.1016/j.biocon.2011.02.004'> https://doi.org/10.1016/j.biocon.2011.02.004</a>" data-for="tooltip-introduccion">[9]</span> sin embargo, estos no son directamente homologables a este nuevo estndar. Contar con su aplicacin permitir hacer seguimiento de la efectividad de cumplimiento de los acuerdos o a las polticas y planes, as como prever los riesgos e impactos de futuros proyectos de desarrollo. </p>

            <p>Aqu presentamos una sntesis infogrfica a partir de la primera evaluacin nacional de los ecosistemas terrestres continentales de Colombia que aplic la metodologa de la LRE <span className="nota-al-pies-number" data-tip="<span>[10]</span>.Etter, A., Andrade, A., Saavedra, K., Amaya P. y Arvalo, P. (2017). <em>Risk Assessment of Colombian Continental Ecosystems: An Application of the Red List of Ecosystems Methodology (v. 2.0)</em>. Bogot: Pontificia Universidad Javeriana y Conservacin Internacional-Colombia. Recuperado de <a href='https://www.researchgate.net/profile/Andres_Etter/publication/325498072_Risk_assessment_of_Colombian_continental_ecosystems_An_application_of_the_Red_List_of_Ecosystems_methodology_v_20/links/5b26f169a6fdcc69746ae24b/Risk-assessment-of-Colombian-continental-ecosystems-An-application-of-the-Red-List-of-Ecosystems-methodology-v-20.pdf'> https://www.researchgate.net/profile/Andres_Etter/publication/325498072_Risk_assessment_of_Colombian_continental_ecosystems_An_application_of_the_Red_List_of_Ecosystems_methodology_v_20/links/5b26f169a6fdcc69746ae24b/Risk-assessment-of-Colombian-continental-ecosystems-An-application-of-the-Red-List-of-Ecosystems-methodology-v-20.pdf</a>" data-for="tooltip-introduccion" >[10]</span>, con el fin de aportar una lnea base y contribuir a la orientacin de la planificacin ambiental del pas, que permita comunicarla en trminos de estndares necesarios para el seguimiento de los objetivos y planes nacionales, y de los compromisos internacionales. Esta propuesta de indicadores de seguimiento de la condicin de los ecosistemas <span className="nota-al-pies-number" data-tip="<span>[11]</span>. Rowland, J. A. et al. (2019). Ecosystem indices to support global biodiversity conservation. <em>Conservation Letters</em>, septiembre, 111. Recuperado de <a href=' https://doi.org/10.1111/conl.12680'> https://doi.org/10.1111/conl.12680</a>" data-for="tooltip-introduccion" >[11]</span> est bien alineada con las metas de la Convencin de Diversidad Biolgica (CBD), las metas de Desarrollo Sostenible (SDG) y las de la Evaluacin de los Ecosistemas del Milenio (MEA), con la finalidad de facilitar un seguimiento estandarizado de las metas globales.</p>
          </>
        }
        {
          language === 'en' &&
          <>

            <p className="mt-lg-3">The biosphere, on which humanity as a whole depends, is undergoing unprecedented alterations at all spatial levels. The different ecosystems that form it are being increasingly impacted and transformed globally, due to the increase of the human populations and the demands for resources derived from their consumption patterns. The main origin of the contemporary environmental crisis is this accelerated transformation and degradation of ecosystems, which are the habitat and essential support of ecological processes and services <span className="nota-al-pies-number" data-tip="<span>[1]</span>. Intergovernmental Science-Policy Platform on Biodiversity and Ecosystem Services (Ipbes). (2019). <em>Global Assessment Report on Biodiversity and Ecosystem Services.</em> Bonn: Autor. Recuperado de <a href='https://ipbes.net/global-assessment'>https://ipbes.net/global-assessment  </a>" data-for="tooltip-introduccion">[1]</span>.</p>

            <p>Biological diversity species, interactions between species and ecosystems is declining at a faster rate than ever before in human history <span className="nota-al-pies-number" data-tip="<span>[2]</span>. Cardinale, B. J., Duffy, J. M., Gonzalez, A., and Hooper, D. U. (2012). Biodiversity loss and its impact on humanity. <em>Nature</em>, 489(7415), 326326. doi: 10.1038/nature11148" data-for="tooltip-introduccion">[2]</span>. Currently, 60% of the earth's surface shows a significant human footprint, and it is estimated that between 1000 and 6000 million hectares show some level of ecological degradation <span className="nota-al-pies-number" data-tip="<span>[3]</span>. Gibbs, H. K. y Salmon, J. M. (2015). Mapping the worlds degraded lands. <em>Applied Geography</em>, 57, 1221. doi: 10.1016/j.apgeog.2014.11.024" data-for="tooltip-introduccion">[3]</span>.</p>

            <p>Human transformation of ecosystems and climate change are putting the future viability of ecosystems at risk, due to their loss of important characteristics and the consequent availability and quality of ecosystem goods and services that depend on them <span className="nota-al-pies-number" data-tip="<span>[2]</span>. Cardinale, B. J., Duffy, J. M., Gonzalez, A. y Hooper, D. U. (2012). Biodiversity loss and its impact on humanity. <em>Nature</em>, 486(7401), 5967. doi: 10.1038/nature11148" data-for="tooltip-introduccion">[2]</span>. For this reason it is of great importance to understand the changes in their characteristics and geographical distributions, and the state of conservation of all ecosystems.</p>

            <p>To anticipate and act effectively on this problematic scenario, we need to understand the risks of ecosystem collapse, since ecosystems are the fundamental reference units for territorial management, because they express in a synthetic manner the composition, functioning and stability of the ecological systems. This has been recognized in global environmental conventions (e.g., Convention on Biological Diversity (CBD) <span className="nota-al-pies-number" data-tip="<span>[4]</span>. Convention of Biological Diversity (CBD). (2010). <em>Strategic Plan for Biodiversity 2011-2020 and the Aichi Biodiversity Targets</em>. Available from:<a href='https://www.cbd.int/doc/meetings/cop/cop-10/official/cop-10-27-add1-en.pdf'> https://www.cbd.int/doc/meetings/cop/cop-10/official/cop-10-27-add1-en.pdf </a>" data-for="tooltip-introduccion">[4]</span>; and the Paris Agreement <span className="nota-al-pies-number" data-tip="<span>[5]</span>. United Nations Climate Change Convention (UNFCCC). (2015).The Paris Agreement. Available from:<a href='https://unfccc.int/process-and-meetings/the-paris-agreement/the-paris-agreement'>https://unfccc.int/process-and-meetings/the-paris-agreement/the-paris-agreement</a>" data-for="tooltip-introduccion">[5]</span>), which have identified the conservation of ecosystems and the maintenance of their integrity, as an essential objective to guarantee the safeguarding of species, ecological processes and the ecosystem services on which society and the planet as whole depend.</p>

            <p>The Red List of Ecosystems (RLE), developed and adopted by the International Union of Conservation of Nature (IUCN) in 2014, is a new global standard for monitoring and documenting the state of ecosystems <span className="nota-al-pies-number" data-tip="<span>[6]</span>. Keith, D. A. <em>et al.</em> (2013). Scientific Foundations for an IUCN Red List of Ecosystems. <em>PLoS ONE</em>, 8(5). doi: 10.1371/journal.pone.0062111" data-for="tooltip-introduccion">[6]</span>.It being recognized as an essential component of the available tools developed to assess the state of biodiversity and associated processes and services, aimed at supporting land use management and conservation. The RLE is also a necessary complement to the IUCN Red List of Species, which has been widely implemented worldwide, by representing the large-scale ecological condition of interactions of species and other important ecological processes.</p>

            <p>The categories and criteria of the RLE are designed to be broadly applicable to all types of ecosystems and geographic areas of the world. Also, to be transparent and scientifically rigorous, and easy to understand by policy makers and the public, who are the ones who finally make the management decisions. Although a major objective of the RLE focuses on biodiversity conservation and restoration, it can also inform a wide range of other activities, including the sustainable management of ecosystem services.</p>

            <p>The goal of the IUCN RLE program is to assess the global status of terrestrial, marine, freshwater and underground ecosystems. It also seeks to give support to the development of national and regional red lists, in order to ensure that all information in national reports can feed global reports of environmental indicators in an orderly manner, and contribute to global conservation and sustainability planning.</p>

            <p>For Colombia, a country in which the biological diversity of ecosystems and species is exceptional and of high strategic value, the implementation of the RLE standard is deemed very important. Several previous studies have shown the increasing process of deterioration of Colombian ecosystems, due to the expansion of the agricultural frontier, the inappropriate land uses and resource extraction practices <span className="nota-al-pies-number" data-tip="<span>[7]</span>. Chaves, S. M. E., Arango, V. N. and Instituto de Investigacin de Recursos Biolgicos Alexander von Humboldt. (1998). <em>Informe nacional sobre el estado de la biodiversidad: Colombia, 1997 </em> (3 vols.). Bogot: Instituto de Investigacin de Recursos Biolgicos Alexander von Humboldt, Ministerio del Medio Ambiente and Programa de las Naciones Unidas para el Medio Ambiente." data-for="tooltip-introduccion">[7]</span>, <span className="nota-al-pies-number" data-tip="<span>[8]</span>. Romero, M., Cabrera, E. and Ortiz, N. (2008). <em>Informe sobre el estado de la biodiversidad en Colombia 2006-2007</em>. Bogot: Instituto de Investigacin de Recursos Biolgicos Alexander von Humboldt. Available from: <a href='http://www.humboldt.org.co/es/test/item/336-informe-sobre-el-estado-de-la-biodiversidad-en-colombia-2006-2007'>http://www.humboldt.org.co/es/test/item/336-informe-sobre-el-estado-de-la-biodiversidad-en-colombia-2006-2007</a>" data-for="tooltip-introduccion" >[8]</span>, <span className="nota-al-pies-number" data-tip="<span>[9]</span>.Etter, A. Mcalpine, C., Seabrook, L. and Wilson, A. (2011). Incorporating temporality and biophysical vulnerability to quantify the human spatial footprint on ecosystems. <em>Biological Conservation, 144</em>(5), 15851594. Available from: <a href='https://doi.org/10.1016/j.biocon.2011.02.004'> https://doi.org/10.1016/j.biocon.2011.02.004</a>" data-for="tooltip-introduccion">[9]</span>. They do not provide however an integrated risk assessment of ecosystems, and are not comparable to this new standard. Applying the RLE in Colombia will allow monitoring how effective they are in complying with the agreements or policies and plans. It will also allow anticipating the risks and impacts of future development projects.</p>

            <p>This book presents an infographic synthesis based on the first national assessment of the continental terrestrial ecosystems of Colombia applying the RLE methodology <span className="nota-al-pies-number" data-tip="<span>[10]</span>.Etter, A., Andrade, A., Saavedra, K., Amaya P. and Arvalo, P. (2017). <em>Risk Assessment of Colombian Continental Ecosystems: An Application of the Red List of Ecosystems Methodology (v. 2.0)</em>. Bogot: Pontificia Universidad Javeriana and Conservacin Internacional-Colombia. Available from: <a href='https://www.researchgate.net/profile/Andres_Etter/publication/325498072_Risk_assessment_of_Colombian_continental_ecosystems_An_application_of_the_Red_List_of_Ecosystems_methodology_v_20/links/5b26f169a6fdcc69746ae24b/Risk-assessment-of-Colombian-continental-ecosystems-An-application-of-the-Red-List-of-Ecosystems-methodology-v-20.pdf'> https://www.researchgate.net/profile/Andres_Etter/publication/325498072_Risk_assessment_of_Colombian_continental_ecosystems_An_application_of_the_Red_List_of_Ecosystems_methodology_v_20/links/5b26f169a6fdcc69746ae24b/Risk-assessment-of-Colombian-continental-ecosystems-An-application-of-the-Red-List-of-Ecosystems-methodology-v-20.pdf</a>" data-for="tooltip-introduccion">[10]</span>, that provides a baseline and contributes to the country's environmental planning, to be communicated in terms of the necessary standards for monitoring national objectives and plans, and international commitments. The RLE will ensure that a more effective and comparable monitoring of the compliance with the international agreements, or with the monitoring national policies and plans, as well helping anticipate the risks and impacts of future development projects. The RLE has already also been expanded proposing indicators for monitoring the condition of ecosystems <span className="nota-al-pies-number" data-tip="<span>[11]</span>. Rowland, J. A. <em>et al</em>. (2019). Ecosystem indices to support global biodiversity conservation. <em>Conservation Letters</em>, septiembre, 111. Available from: <a href=' https://doi.org/10.1111/conl.12680'> https://doi.org/10.1111/conl.12680</a>" data-for="tooltip-introduccion">[11]</span> to help monitor agreements aligned with the goals of the Convention on Biological Diversity (CBD), the goals of Sustainable Development (SDG) and the Millennium Ecosystem Assessment (MEA), in order to facilitate an standardized follow-up of global goals.</p>
          </>
        }
        <ReactTooltip
          id="tooltip-introduccion"
          place="top"
          html={true}
          clickable={true}
          effect={'solid'}
          type={'info'}
          event={'click'}
          globalEventOff={'click'}
          className={'nota-al-pie'}
        />
      </>
    )
  }


  const state = useTrackedState();
  const { language } = state;
  const isMobile = useIsMobile();
  const [heightContainer, containerRef, headerSpace] = useHeightContainer();


  return (
    <div id="introduccion">
      <div className="container-fluid row mx-0 py-5 py-lg-3  flex-lg-nowrap  justify-content-between  align-items-start px-0">
        <div className="col-lg-6 pl-5 d-flex justify-content-center px-5">
          <div className="introduccion-textos">

            <div className="title-sticky"
              style={{
                top: `${headerSpace}px`
              }}
            >
              {language === 'es' &&
                <>
                  <h2>Introduccin</h2>
                </>
              }
              {language === 'en' &&
                <>
                  <h2>Introduction</h2>
                </>
              }

            </div>
            <div className="introduccion-textos introduccion">
              <Textos />
            </div>
          </div>
        </div>

        <div 
          className="col-lg-8 px-0 imagenes-scroll imagenes-scrolll-introduccion m-0 mt-5 mt-lg-0"
          style={{
            position: "sticky",
            top: heightContainer/2
          }}  
        >
          <div            
            ref={containerRef}
            style={{
              height: isMobile ? "auto": `${heightContainer}px`
            }}
          >
            <img src={`./img/introduccion/ambiente3.jpg`} alt="imagen de ambiente 1" className="img-fluid w-100" />
          </div>
        </div>
      </div>
    </div>
  )
}


export default Introduccion;